.lonyo-career{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        transition: all 0.4s;
        @include max (991px){
            padding: 20px;
        }
        @include max (767px){
            box-shadow: 0px 4px 80px 0px #00000014;
            margin-bottom: 24px;
        }
        h5{
            margin-bottom: 15px;
        }
        p{
            font-size: 21px;
            line-height: 30px;
            margin-bottom: 24px;
            color: var(--heading-color);
        }
        &.hovered{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-content{
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                font-family: $heading-font;
                font-size: 20px;
                font-weight: 700;
                line-height: 30px;
                color: var(--dark-color2);
                &:last-child{
                    margin-right: 0;
                }
                img{
                    margin-right: 10px;
                    margin-top: -3px;
                }
            }
        }
    }
    &-single-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        margin-bottom: 24px;
        img{
            
            border-radius: 10px;
            object-fit: cover;
            transition: all 0.4s;
            width: 100%;
            height: 100%;
            &:hover{
             transform: scale(1.1);
            }
        }
    }
    &-single-content{
        p{
            color: var(--heading-color);
        }
    }
    &-single-data{
        padding-bottom: 30px;
        h3{
            padding-bottom: 20px;
        }
        ul{
            li{
                color: var(--heading-color);
                position: relative;
                padding-left: 20px;
                margin-bottom: 5px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    top: 12px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
            }    
        }
    }
    &-single-sidebar{
        border-radius: 10px;
        padding: 38px 30px;
        background-color: var(--light-color);
        position: sticky;
        top: 120px;
        @include max (991px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        p{
            color: var(--light-color5);
        }
        .sidebar-title{
            padding-bottom: 24px;
            position: relative;
            &::before{
                bottom: 20px;
                left: 0;
                width: 260px;
                height: 2px;
                content: "";
                position: absolute;
                background-color: var(--heading-color);
            }
        }
        ul{
            li{
                font-size: 21px;
                font-weight: 700;
                line-height: 30px;
                margin-bottom: 15px;
                color: var(--heading-color);
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    padding-right: 10px;
                    padding-bottom: 3px;
                }
            }
        }
    }
}
.mt-24{
    margin-top: 24px;
    padding: 13px 40px!important;
}