.lonyo-term-condition{
    &-content{
        padding-bottom: 30px;
        padding-left: 50px;
        @include max (1199px){
            padding-left: 30px;
        }
        @include max (991px){
            padding-left: 0;
        }
        &:last-child{
            padding-bottom: 0;
        }
        .title-text{
            color: var(--dark-color2);
            margin-bottom: 30px;
            @include max (991px){
                padding-top: 40px;
            }
            @include max (767px){
                padding-top: 30px;
            }
        }
        h3{
            padding-bottom: 20px;
        }
        p{
            color: var(--dark-color2);
            margin: 0;
        }
    }
}