.lonyo-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.lonyo-section-padding2{
    padding: 34px 0 106px;
    @include max (991px){
        padding: 34px 0 76px;
    }
    @include max (767px){
        padding: 14px 0 32px;
    }
}
.lonyo-section-padding3{
    padding: 0 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.lonyo-section-padding4{
    padding: 0 0 35px;
    @include max (1600px){
        padding: 0 0 50px;
    }
    @include max (991px){
        padding: 0 0 35px;
    }
    @include max (767px){
        padding: 0 0 10px;
    }
}
.lonyo-section-padding5{
    padding: 35px 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}

// section title

.lonyo-section-title{
    padding-bottom: 60px;
    @include max (991px){
        padding-bottom: 40px;
        text-align: center;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
    }
    .text2{
        color: var(--light-color2);
    }
    
}
// default content
.lonyo-default-content{
    h2{
        padding-bottom: 20px;
    }
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
}
.pl-50{
    padding-left: 68px;
    @include max (991px){
        padding-left: 0;
    }
}
.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.data{
    max-width: 636px;
}