.lonyo-footer{
  &-section{
    padding: 130px 0 0;
    position: relative;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
    }
  }
  &-menu{
    h4{
      margin-bottom: 24px;
     }
    ul{
      li{
        margin-bottom: 12px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          color: var(--heading-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      h4{
        color: var(--light-color);
      }
      ul{
        li{
          a{
            color: var(--light-color);
            &::before{
              content: " ";
              left: 0;
              bottom: -3px;
              width: 0;
              height: 1px;
              position: absolute;
              transition: all .4s;
              background: var(--light-color);
            }
            &:hover::before{
              width: 100%;
            }
          }
        }
      }
    }
  }
  &-textarea{
    padding-right: 30px;
    @include max (1399px){
      margin-bottom: 30px;
      max-width: 500px;
    }
    img{
      padding-bottom: 24px;
    }
    p{
      margin-bottom: 24px;
    }
   &.textarea2{
    p{
      color: var(--light-color);
    }
   }
  }
  &-menu-wrap {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &-bottom-text{
    margin-top: 80px;
    border-top: 1px solid #142D6F1A;
    padding: 30px 0;
    text-align: center;
    @include max (767px){
      margin-top: 60px;
    }
    p{
      font-size: 18px;
      line-height: 25px;
    }
    &.text2{
      p{
        color: var(--light-color);
      }
    }
  }
  &-shape{
    position: absolute;
    width: 90px;
    height: 90px;
    border-radius: 100px;
    background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
    right: 100px;
    top: 45%;
    @include max (1800px){
      display: none;
    }
  }
  &-shape2{
    position: absolute;
    width: 90px;
    height: 90px;
    background: linear-gradient(47.56deg, #A3BDFF -40.67%, #142D6F 78.6%);
    border-radius: 100px;
    right: 100px;
    top: 45%;
    @include max (1800px){
      display: none;
    }
  }
}
.pl-30{
  padding-left: 30px;
  @include max (1399px){
    padding-left: 50px;
  }
  @include max (1199px){
    padding-left: 0;
    padding-top: 40px;
  }
}
.pl-31{
  padding-left: 30px;
  @include max (1399px){
    padding-left: 0;
  }
  @include max (1199px){
    padding-top: 40px;
  }
 
}
