
// counter

.lonyo-counter{
  &-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 130px;
    flex-wrap: wrap;
    @include max (991px){
        padding-top: 80px;
    }
    @include max (767px){
      flex-wrap: wrap;
        padding-top: 60px;
    }
  }
  &-item {
    text-align: center;
    @include max (767px){
      &:last-child{
        padding-top: 24px;
      }
    }
    @include max (465px){
      &:nth-child(3){
        padding-top: 24px;
      }
    }
    p{
      padding-top: 12px;
      font-weight: 600;
      color: var(--heading-color);
      @include max (767px){
       padding-top: 0;
      }
    }
  }
  &-data {
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    height: 75px;
    @include max (1199px){
      height: 50px;
    }
    @include max (767px){
      justify-content: center;
    }
    &::before{
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 16px;
      background-color: var(--accent-color);
      @include max (767px){
          display: none;
      }
    }
  }
}


.lonyo-counter-data > span {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 2s ease;
  transform: translateY(0);
  line-height: 1;
  
}
.data2 > span{
  width: 15%;
  @include max (1199px){
    width: 10%;
  }
}
.lonyo-counter-data > span span {
  flex: 0 0 100%;
  height: 100%;
}
.lonyo-counter-data:nth-child(1) > span {
  transition-delay: 0s;
}
.lonyo-counter-data:nth-child(2) > span {
  transition-delay: 0.375s;
}
.lonyo-counter-data:nth-child(3) > span {
  transition-delay: 0.75s;
}
.lonyo-counter-data:nth-child(4) > span {
  transition-delay: 1.125s;
}
.lonyo-counter-data:nth-child(5) > span {
  transition-delay: 1.5s;
}
.lonyo-counter-data:nth-child(6) > span {
  transition-delay: 1.875s;
}
.lonyo-counter-data:nth-child(7) > span {
  transition-delay: 2.25s;
}
.lonyo-counter-data:nth-child(8) > span {
  transition-delay: 2.625s;
}
.lonyo-counter-data:nth-child(9) > span {
  transition-delay: 3s;
}
.lonyo-counter-data:nth-child(10) > span {
  transition-delay: 3.375s;
}
.lonyo-counter-data:nth-child(11) > span {
  transition-delay: 3.75s;
}
.lonyo-counter-data:nth-child(12) > span {
  transition-delay: 4.125s;
}
.lonyo-counter-data:nth-child(13) > span {
  transition-delay: 4.5s;
}
.lonyo-counter-data:nth-child(14) > span {
  transition-delay: 4.875s;
}
.lonyo-counter-data:nth-child(15) > span {
  transition-delay: 5.25s;
}
.lonyo-counter-data:nth-child(16) > span {
  transition-delay: 5.625s;
}
.lonyo-counter-data:nth-child(17) > span {
  transition-delay: 6s;
}
.lonyo-counter-data:nth-child(18) > span {
  transition-delay: 6.375s;
}
.lonyo-counter-data:nth-child(19) > span {
  transition-delay: 6.75s;
}
.lonyo-counter-data:nth-child(20) > span {
  transition-delay: 7.125s;
}


.counter-card{
  h2{
    color: var(--white-color);
    font-size: 40px;
    line-height: 48px;
    height: 40px;
  }
  span.text{
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    padding-right: 12px;
    color: var(--white-color);
  }
}