.lonyo-skill{
    &-wraper{
        display: flex;
        align-items: center;
        gap: 50px;
        position: relative;
        @include max (767px){
            display: block;
        }
    }
    &-wrap{
        @include max (991px){
            margin-top: 40px;
        }
        h4{
            margin-bottom: 20px;
        }

    }
    &-icon{
        flex-shrink: 0;
        display: inline-block;
        animation: animName 15s linear infinite;
    }
    &-icon2{
        position: absolute;
        left: 37px;
        top: 37px;
        @include max (767px){
            top: 38px;
        }
    }
    &-item{
        margin-bottom: 15px;
        &:last-child{
            margin-bottom: 0!important;
        }
        &.item2{
            margin-bottom: 30px;
            
        }
    }
    &-title{
        h5{
            margin-bottom: 15px;
        }
        &.title2{
            h5{
                color: var(--white-color);
            }
        }
        &.title3{
            position: relative;
            &::before{
                position: absolute;
                content: "";
                top: 12px;
                left: -17px;
                width: 8px;
                height: 8px;
                border-radius: 100px;
                background-color: var(--heading-color);
            }
        }
    }
    &-line{
        position: relative;
        height: 12px;
        background: var(--white-bg);
        transform: scaleX(0);
        transform-origin: left;
        border-radius: 30px;
        border: 1px solid var(--accent-color);
        animation: animate 1s cubic-bezier(1,0,.5,1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0!important;
        }
    }
    &-bar{
        margin: -1px 0px 0 -2px;
        height: 11px;
        position: absolute;
        border-radius: 30px;
        transform-origin: left;
        background: var(--accent-color);
        animation: animate 1s 1s cubic-bezier(1,0,.5,1) forwards;
        &.bar2{
            border-radius: 5px;
            height: 20px;
            margin: 0 0px 0 -2px;
            &::after{
                position: absolute;
                right: -32px;
                top: -44px;
                font-size: 16px;
                font-weight: 400;
                padding: 1px 8px;
            }
        }
        
    &::before{
        position: absolute;
        content: "";
        height: 0;
        right: 0;
        top: -14px;
        width: 0;
        border: 7px solid transparent;
        border-bottom-width: 0px;
        border-right-width: 0px;
        border-top-style: #f0f0f0;
    }
    &::after{
        position: absolute;
        right: 0;
        top: -44px;
        color: var(--heading-color);
        font-size: 20px;
        font-weight: 700;
        padding: 1px 8px;
        border-radius: 3px;
    }
    &.extra-bar{
        &::after{
            color: var(--white-color);
        }
    }

    }
    &-line2{
        position: relative;
        height: 12px;
        background: var(--white-bg);
        transform: scaleX(0);
        transform-origin: left;
        border-radius: 30px;
        border: 1px solid var(--accent-color);
        animation: animate 1s cubic-bezier(1,0,.5,1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0!important;
        }
    }
    &-bar2{
        margin: 0 0px 0 -2px;
        height: 10px;
        position: absolute;
        border-radius: 53px;
        transform-origin: left;
        background: var(--accent-bg);
        animation: animate 1s 1s cubic-bezier(1,0,.5,1) forwards;
  
    &::after{
        position: absolute;
        right: 0;
        top: -44px;
        font-size: 24px;
        line-height: 32px;
        font-weight: 700;
        padding: 1px 8px;
        border-radius: 6px;
        color: var(--heading-color);
        }
        &.extra-bar{
            &::after{
                color: var(--white-color);
            }
        }
    }
}
.line3{
    border: 1px solid transparent;
    background-color: var(--light-color);
}

.bar-one{
    width: 80%;
}
.bar-one::after{
    content: "80%";
}
.bar-two{
    width: 75%;
}
.bar-two::after{
    content: "75%";
}
.bar-three{
    width: 99.9%;
}
.bar-three::after{
    content: "99.9%";
}
.bar-four{
    width: 95%;
}
.bar-four::after{
    content: "95%";
}
.bar-five{
    width: 70%;
}
.bar-five::after{
    content: "70%";
}
.bar-six{
    width: 85%;
}
.bar-six::after{
    content: "85%";
}




@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
