.lonyo-faq{
    &-item{
      padding-bottom: 24px;
      &.item2{
        padding-bottom: 24px;
        border-bottom: 1px solid #142D6F1A;
        margin-bottom: 24px;
        &:last-child{
          padding-bottom: 0;
          border-bottom: none;
          margin-bottom: 60px;
          @include max (991px){
            margin-bottom: 30px;
          }
        }
      }
      &:last-child{
        padding-bottom: 0;
      }
      &.open {
        .lonyo-faq-body{
            display: block;
          }
      }
    }
    &-header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 15px;
    }
    &-body{
      display: none;
      p{
        font-size: 18px;
        line-height: 1.39em;
      }
      &.body2{
        p{
          font-size: 20px;
          line-height: 1.3em;
          max-width: 900px;
        }
      }
    }
    &-shape{
      position: relative;
      &::before{
        content: "";
        position: absolute;
        bottom: 60px;
        right: -18%;
        width: 130px;
        height: 130px;
        border-radius: 100px;
        background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
        @include max (1800px){
          display: none;
        }
      }
    }
}
.lonyo-faq-item.open {
  img.plasicon{
    opacity: 1!important;
  }
  img.mynusicon{
    opacity: 0;
    display: block;
  }
  
}
img.mynusicon {
  opacity: 1;
  position: relative;
}
img.plasicon {
  opacity: 0;
  position: absolute;
}

.lonyo-active-icon{
    flex-shrink: 0;
}
.faq-btn{
  text-align: center;
}