.lonyo-p{
    &-wrap{
        position: relative;
        &:hover{
            .lonyo-p-content{
                opacity: 1;
                bottom: 30px;
            }
            .lonyo-p-thumb img{
                scale: 1.1;
            }

        }
        &.wrap2{
            padding: 0 15px;
        }
    }
    &-thumb{
        position: relative;
        display: block;
        margin-bottom: 24px;
        text-align: center;
        overflow: hidden;
        border-radius: 10px;
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            opacity: 0;
            transition: .4s;
            background: linear-gradient(180deg, rgba(41, 32, 210, 0) 0%, rgba(41, 32, 210, 0.6) 100%);
        }
        &:hover::before {
            opacity: 1;
        }
        img{
            border-radius: 10px;
            width: 100%;
            transition: all 0.4s;
            object-fit: cover;
        }
    }
    &-content{
        display: flex;
        z-index: 1;
        align-items: center;
        gap: 24px;
        background-color: #fff;
        border-radius: 10px;
        padding: 20px 27px;
        position: absolute;
        bottom: 0;
        left: 30px;
        transition: all .4s;
        opacity: 0;
        justify-content: center;
        @include max (991px){
            left: 15px;
            padding: 15px 20px;
            gap: 15px;
            bottom: 15px!important;
        }
    }
    &-btn{
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px!important;
        background-color: var(--accent-bg);
        svg{
            transition: all 0.4s;
            transform: rotate(-45deg);
        }
       
        
    }
    // inner page
    &-d-thumb{
        margin-bottom: 24px;
        overflow: hidden;
        position: relative;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            &:hover{
                transform: scale(1.1);
            }
        }
    }
    &-d-thumb3{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        margin-top: 30px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            &:hover{
                transform: scale(1.1);
            }
        }
    }
    &-d-content{
        padding-bottom: 30px;
        h3{
            padding-bottom: 20px;
        }
        p{
            color: var(--heading-color);
        }
        ul{
            li{
                position: relative;
                padding-left: 20px;
                color: var(--heading-color);
                margin-bottom: 5px;
                &:first-child{
                    margin-top: 30px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    top: 12px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-color);
                }
            }
        }
    }
    &-d-sidebar{
        border-radius: 10px;
        padding: 30px;
        background-color: var(--light-color);
        position: sticky;
        top: 120px;
        @include max (991px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        .sidebar-title2{
            position: relative;
            padding-bottom: 24px;
            &::before{
                width: 226px;
                height: 2px;
                bottom: 20px;
                left: 0;
                content: "";
                position: absolute;
                background-color: var(--heading-color);
            }
        }
        ul{
            li{
                font-size: 21px;
                line-height: 30px;
                font-family: $heading-font;
                color: var(--light-color3);
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                span{
                    font-family: $heading-font;
                    font-size: 20px;
                    font-weight: 700;
                    color: var(--light-color3);
                    line-height: 30px;
                    margin-right: 5px;
                }
                a{
                    color: var(--accent-bg);
                    &.link{
                        position: relative;
                        &::before{
                            width: 210px;
                            height: 2px;
                            bottom: 0;
                            right: 0;
                            content: "";
                            position: absolute;
                            background-color: var(--accent-bg);
                        }
                    }
                }
            }
        }
    }
}
.lonyo-portfolio-menu{
    ul{
        li{
            display: inline-block;
            border: 1px solid var(--heading-color);
            color: var(--heading-color);
            border-radius: 50px;
            padding: 9px 29px;
            cursor: pointer;
            margin-left: 24px;
            font-size: 16px;
            font-weight: 700;
            @include max(767px){
                margin-bottom: 24px;
            }
            @include max(425px){
            &:nth-child(3){
                margin-bottom: 0;
            }
            }
            
            &:last-child{
            margin-bottom: 0;
            }
           
            &.active{
                background: var(--accent-color);
                color: var(--white-color);
                border: 1px solid var(--accent-color);
            }
        }
    }
}

