.lonyo-social{
    &-wrap{
        ul{
            li{
                display: inline-block;
                margin-right: 10px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    width: 35px;
                    height: 35px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all 0.4s;
                    border-radius: 100px;
                    border: 1px solid var(--heading-color);
                    path{
                        transition: all 0.4s;
                    }
                    &:hover{
                        background-color: var(--heading-bg);
                        path{
                            fill: var(--white-color);
                        }
                    }
                }
            }
        }
        &.wrap2{
            ul{
                li{
                    margin-right: 10px;
                    margin-left: 0;
                    &:last-child{
                        margin-left: 0;
                        margin-right: 0;
                    }
                    a{
                        transition: all 0.4s;
                        border: 1px solid var(--white-color);
                        &:hover{
                            background-color: var(--white-bg);
                            border: 1px solid var(--accent-bg);
                            path{
                                fill: var(--heading-color);
                            }
                        }
                        path{
                            fill: var(--white-color);
                        }
                    }
                }
            }
        }
        &.blog-social{
            ul{
                li{
                    a{
                        width: 30px;
                        height: 30px;
                        transition: all 0.4s;
                        border-radius: 100px;
                        border: 1px solid var(--heading-color);
                        &:hover{
                            border: 1px solid transparent;
                            background-color: var(--accent-bg);
                            path{
                                fill: var(--white-color);
                            }
                        }
                    }
                }
            }
        }
    }
}

.lonyo-team-social{
    margin-top: 12px;
    transition: all 0.4s;
    display: none;
    ul{
        li{
            display: inline-block;
            margin-right: 10px;
            &:last-child{
                margin-right: 0;
            }
            a{
                width: 28px;
                height: 28px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 100px;
                border: 1px solid var(--heading-color);
                transition: all 0.4s;
                path{
                    transition: all 0.4s;
                }
                &:hover{
                    background-color: var(--white-bg);
                    border: 1px solid var(--white-color);
                    path{
                        fill: var(--accent-color)!important;
                    }
                }
            }
        }
    }
}