.lonyo-brand{
    &-section{
        padding: 100px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        .title{
            text-align: center;
            padding-bottom: 60px;
            margin: 0;
            @include max (991px){
                padding-bottom: 30px;
            }
        }
        
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 60px;
        }
    }
}
.lonyo-brand-slider-item .slick-slide .slick-cloned{
    padding: 0 15px;
}