.lonyo-team{
    &-wrap{
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .lonyo-team-social{
                display: block;
                ul{
                    li{
                        a{
                            border: 1px solid var(--white-color);
                        }
                    }
                }
            }
            .lonyo-team-content{
                height: 150px;
                background-color: var(--accent-color);
                h5,p{
                    color: var(--white-color);
                }
            }
            .lonyo-team-thumb img{
                transform: scale(1.05);
            }
        }
    }
    &-thumb{
        position: relative;
        z-index: 0;
        overflow: hidden;
        border-radius: 10px;
        transition: all 0.4s;
        img{
            width: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    &-content{
        position: absolute;
        transition: all .4s;
        padding: 24px 0;
        background-color: var(--light-color);
        text-align: center;
        border-radius: 0 0 8px 8px;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 110px;
        h5{
            padding-bottom: 5px;
        }
        p{
            margin-bottom: 0;
        }
    }
    // inner page
    &-d-contact-box{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-d-thumb{
        border-radius: 10px;
        position: relative;
        overflow: hidden;
        margin-bottom: 30px;
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
        img{
            transition: all 0.4s;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }
    &-d-contact-wrap {
        display: flex;
        gap: 24px;
        align-items: center;
        margin-bottom: 24px;
    }
    &-d-contact-icon{
        width: 60px;
        height: 60px;
        border-radius: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--light-color);
    }
    &-d-contact-data{
        h6{
            color: var(--heading-color);
            padding-bottom: 5px;
        }
        p{
            color: var(--heading-color);
        }
    }
    &-d-content{
        h2{
            margin-bottom: 20px;
        }
        h5{
            margin-bottom: 24px;
            color: var(--accent-color);
        }
        p{
            font-size: 21px;
            line-height: 30px;
            color: var(--dark-color2);
        }
    }
    &-d-skill-wrap{
        margin-top: 10px;
        h3{
            padding-bottom: 20px;
        }
        p{
            color: var(--heading-color);
            margin-bottom: 30px;
            font-size: 21px;
            line-height: 30px;
        }
    }
}

.pl-103{
    padding-left: 100px;
    @include max (1199px){
        padding-left: 50px;
    }
    @include max (991px){
        padding-left: 0px;
    }
}
.pl-20{
    padding-left: 20px;
}
.pl-102{
    padding-left: 100px;
    @include max (1199px){
        padding-left: 0;
    }
}

.lonyo-team-slider .slick-slide {
    margin: 0 12px;
}
.lonyo-team-slider .slick-dots button {
    display: block;
    padding: 0;
    border: none;
    text-indent: -9999px;
    transition: .3s;
    width: 10px;
    height: 10px;
    background: #001A3D1A;
    transition: all 0.4s;
    border-radius: 100px;
}
.lonyo-team-slider ul.slick-dots {
    bottom: -50px;
    left: 50%;
    position: absolute;
    display: flex;
    gap: 15px;
    transform: translate(-50%, -50%);
    @include max (767px){
        bottom: -40px;
    }
}
.lonyo-team-slider .slick-dots li {
    display: flex;
    align-items: center;
    justify-content: center;
}
.lonyo-team-slider .slick-dots li.slick-active {
    width: 23px;
    height: 23px;
    border-radius: 100px;
    border: 1px solid var(--accent-color);
    button{
        background-color: var(--accent-color);
    }
}