.lonyo-t{
    &-wraper{
        position: relative;
        &::before{
            content: "";
            position: absolute;
            top: -100px;
            left: -220px;
            width: 150px;
            height: 150px;
            border-radius: 100px;
            transform: rotate(180deg);
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
            @include max (1800px){
                display: none;
            }
        }
    }
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 24px;
    }
    &-text{
        p{
            margin-bottom: 24px;
        }
    }
    &-overlay{
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 1;
    }
    &-author{
        display: flex;
        align-items: center;
        gap: 15px;
        &-data{
            p{
                font-weight: 500;
                margin-bottom: 5px;
            }
            span{
                font-size: 18px;
                line-height: 25px;
            }
        }
    }
}
