// fonts variable
$body-font: "DM Sans", sans-serif!default;
$heading-font: "Plus Jakarta Sans", sans-serif !default;


// color variable
:root {
  --heading-color: #142D6F;
  --heading-bg: #142D6F;
  --body-color: #A3BDFF;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --light-color: #F6F4EF;
  --light-color2: #A3BDFF;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 20px;
$buton_line_height: 1.3em;
$buton_border_radius: 3px;
$button_padding: 18px 39px;
$button_font_weight: 500;
$button_font_family: $body-font;