.lonyo-contact-us{
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        @include max (991px){
            margin-bottom: 24px;
        }
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
        img{
            width: 100%;
            height: 100%;
            border-radius: 10px;
            transition: all 0.4s;
            object-fit: cover;
        }
    }
    &-info-box{
        padding-top: 100px;
        @include max (991px){
            padding-top: 80px;
        }
        @include max (767px){
            padding-top: 60px;
        }
    }
    &-info-wrap{
        gap: 24px;
        display: flex;
        background-color: var(--accent-bg);
        padding: 30px 0;
        justify-content: center;
        border-radius: 10px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-info-icon{
    }
    &-info-data{
        h5{
            margin-bottom: 10px;
            color: var(--white-bg);
        }
        span{
            font-size: 21px;
            line-height: 30px;
            color: var(--white-bg);
            display: block;
            margin-bottom: 5px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
.lonyo-contact-us-map {
    width: 100%;
    height: 750px;
    @include max (991px){
        height: 400px;
    }
}