.breadcrumb{
  &-wrapper{
    z-index: 0;
    text-align: center;
    padding: 280px 0 130px;
    position: relative;
    transition: all 0.4s;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    &::before{
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      background: #2920D299;
    }
    @include max(1199px) {
      padding: 200px 0 100px;
    }
    @include max(767px) {
      padding: 150px 0 100px;

    }
    
    @include max(575px) {
      padding: 150px 0 80px;
    }
  }
  &-content {
    position: relative;
  }

  &-menu{
    ul{
      li{
        font-weight: 600;
        line-height: 20px;
        line-height: 1;
        z-index: 0;
        padding: 0 10px;
        position: relative;
        display: inline-block;
        color: var(--white-color);
        font-family: $heading-font;
        &:first-child{
          padding: 0;
        }
        &:last-child{
          padding: 0;
        }
        &:before {
          content: '';
          right: -12px;
          top: 2px;
          font-size: 20px;
          z-index: -1;
          line-height: 12px;
          position: absolute;
          color: var(--light-color2);
        }
        &:last-child:before{
          content: none;
        }
        a{
          color: var(--white-color);
          font-weight: 400;
          line-height: 20px;
          font-family: $heading-font;
        }
      }
    }
    &-wrap{
      padding-top: 50px;
      @include max (1199px){
        padding-top: 30px;
      }
    }
  }
}




.lonyo-errors{
  &-section{
    padding: 330px 0 130px;
    @include max(991px) {
      padding: 250px 0 100px;
    }
    @include max(767px) {
      padding: 170px 0 80px;
    }
  }
  &-content{
    max-width: 650px;
    margin: 0 auto;
    text-align: center;
    img{
      margin-bottom: 40px;
    }
    h2{
      margin-bottom: 15px;
    }
  }
}