input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 400;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  @include max(767px) {
    height: 100px;
  }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--heading-color);
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--heading-color);
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--heading-color);
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--heading-color);
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--heading-color);
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--heading-color);
   opacity: 1;
}


[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

// footer sebscription
.lonyo-subscription{
   &-field{
      text-align: center;
      position: relative;
      max-width: 600px;
      margin: 0 auto;
      .sub-btn{
         position: absolute;
         top: 5px;
         right: 5px;
         padding: 18px 28px;
         color: var(--light-color);
         font-weight: 500;
         @include max (767px){
            position: relative;
            width: 100%;
            margin-top: 24px;
            @include max (767px){
               margin-top: 15px;
               justify-content: center;
            }
         }
       }
      input{
         padding: 14px 41% 14px 20px;
         border-radius: 3px;
         height: 72px;
         transition: all .4s;
         border: 1px solid var(--heading-color);
         @include max (767px){
            padding: 18px;
         }
      }
   }
   &-field2{
      input{
         height: 72px;
         transition: all .4s;
         border: 1px solid var(--heading-color);
         border-radius: 3px;
         margin-bottom: 15px;
         padding: 14px 20px;
         @include max (767px){
            padding: 18px;
         }
      }
   }
   &-field3{
      input{
         height: 72px;
         transition: all .4s;
         color: var(--white-color);
         border: 1px solid var(--white-color);
         border-radius: 3px;
         margin-bottom: 15px;
         padding: 14px 20px;
         @include max (767px){
            padding: 18px;
         }
      }
   }
}


.lonyo-subscription-field3 input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--white-color);
    opacity: 1;
}
.lonyo-subscription-field3 input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--white-color);
   opacity:  1;
}
.lonyo-subscription-field3 input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--white-color);
   opacity:  1;
}
.lonyo-subscription-field3 input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--white-color);
   opacity: 1;
}
.lonyo-subscription-field3 input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--white-color);
   opacity: 1;
}

.lonyo-subscription-field3 input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--white-color);
   opacity: 1;
}
// contact box start

.lonyo-contact{
   &-thumb{
      @include max (991px){
         padding-top: 40px;
      }
       img{
           border-radius: 10px;
           @include max (991px){
            width: 100%;
         }
       }
   }
   &-box{
       background: var(--light-color);
       border-radius: 10px;
       padding: 30px;
   }
   &-title{
      padding-bottom: 30px;
      &.title2{
         padding-bottom: 50px;
         @include max (991px){
            padding-bottom: 40px;
         }
         @include max (767px){
            padding-bottom: 30px;
         }
      }
   }
}

.lonyo-main-field{
   input{
      background-color: var(--white-bg);
      padding: 18px 30px;
      border-radius: 50px;
      border: 1px solid #001A3D1A;
      margin-bottom: 30px;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      color: #001A3D80;
   }
   &-textarea{
      background-color: var(--white-bg);
      border: 1px solid #001A3D1A;
      padding: 18px 20px 20px 30px;
      border-radius: 20px;
      margin-bottom: 24px;
      height: 180px;
      textarea{
         padding: 0;
      }
   }
   &.field2{
      input{
         border: none;
         &::placeholder{
            color: var(--heading-color);
         }
      }
   }
   svg{
      margin-left: 10px;
      margin-top: 0!important;
   }
}
.lonyo-main-field input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
