.lonyo-hero{
    &-shape {
        position: absolute;
        top: 80px;
        left: 20px;
        z-index: -1;
        transform: translate(-50px, -50px);
        @include max (991px){
            left: 100px;
        }
        @include max (767px){
            display: none;
        }
    }
    &-shape2 {
        img{
            position: absolute;
            top: 0;
            left: 0;
        }
        &::before{
            position: absolute;
            content: "";
            top: 40%;
            right: 100px;
            width: 150px;
            height: 150px;
            border-radius: 100px;
            background: linear-gradient(48.01deg, #a3bdff -82.43%, #142d6f 89.75%);
            @include max (1800px){
                display: none;
            }
        }
        @include max (991px){
            left: 100px;
        }
        @include max (767px){
            display: none;
        }
    }
    &-shape3 {
            position: absolute;
            top: 0;
            left: 0;
            // width: 350px;
            // height: 350px;
            // border-radius: 180px;
            // background: linear-gradient(48.01deg, #a3bdff -82.43%, #142d6f 89.75%);
    }
}

// end hero
.lonyo-content{
    &-shape2{
        width: 120px;
        height: 120px;
        bottom: 170px;
        right: 60px;
        position: absolute;
        border-radius: 100px;
        background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
        @include max (1800px){
            display: none;
        }
    }
}