
.lonyo-default-btn,
.lonyo-subcribe-btn,
#lonyo-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  width: fit-content;
  position: relative;
  z-index: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--body-color);
  background-color: var(--heading-bg);
  overflow: hidden;
  transform-style: preserve-3d;
  border: 1px solid var(--heading-color);
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.5s, border 0.5s;
  }
  &::after{
    content: "";
    position: absolute;
    top: 1%;
    left: 0%;
    width: 300px;
    height: 300px;
    background-color: #fff;
    border-color: transparent;
    border-radius: 50%;
    transform: translate(-10px, -70px) scale(0.1);
    opacity: 0;
    z-index: -1;
    transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
  }
  &:hover::after {
    opacity: 1;
    transform: scaleX(1.5) scaleY(1.5);
  }
  &.lonyo-header-btn{
    padding: 12px 24px;
    color: var(--light-color);
    @include max (767px){
      display: none;
    }
    &.btn2{
      color: var(--heading-color);
      background-color: var(--light-color2);
    }
    &:hover{
      color: var(--heading-color);
      &::after {
        opacity: 1;
        transform: scaleX(1.5) scaleY(1.5);
      }
    }
    &::before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: opacity 0.5s, border 0.5s;
    }
    &::after{
      content: "";
      position: absolute;
      top: 1%;
      left: 0%;
      width: 200px;
      height: 300px;
      background-color: #fff;
      border-color: transparent;
      border-radius: 50%;
      transform: translate(-10px, -70px) scale(0.1);
      opacity: 0;
      z-index: -1;
      transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
    }
  }
  &.hero-btn{
    padding: 18px 25px;
    color: var(--white-color);
    &:hover{
      color: var(--heading-color);
    }
  }
  &.subscrib-btn{
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
  }
  &.subscrib-btn2{
    color: var(--heading-color)!important;
    background-color: var(--body-color)!important;
  }
  &.video-btn{
    color: var(--light-color);
    border: 2px solid var(--light-color);
    padding: 16px 22px;
    &:hover{
      color: var(--heading-color);
    }
  }
  &.faq-btn2{
    text-align: center;
    color: var(--light-color);
    &:hover{
      color: var(--heading-color);
    }
    &::after{
      content: "";
      position: absolute;
      top: 1%;
      left: 0%;
      width: 400px;
      height: 400px;
      background-color: #fff;
      border-color: transparent;
      border-radius: 50%;
      transform: translate(-10px, -70px) scale(0.1);
      opacity: 0;
      z-index: -1;
      transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
    }
    &:hover::after {
      opacity: 1;
      transform: scaleX(1.5) scaleY(1.5);
    }
  }



  &.d-block {
    width: 100%;
  }
  &.pill {
    border-radius: 50px;
  }
 
  &:focus {
    box-shadow: none;
  }
}

.lonyo-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}


  
