.lonyo-cta{
    &-section{
        padding: 120px 0 0;
        position: relative;
        
        @include max (991px){
            padding: 80px 0 0;
        }
        @include max (767px){
            padding: 60px 0 0;
        }
    }
    &-wrap{
        @include max (991px){
            padding-bottom: 80px;
        }
        @include max (767px){
            padding-bottom: 60px;
        }
        h2,p{
            color: var(--light-color);
        }
    }
    &-thumb{
        text-align: center;
        position: relative;
        z-index: 0;
        img{
            position: relative;
            z-index: 1;
            border-radius: 53px 53px 0 0;
        }
        &::before{
            content: "";
            position: absolute;
            top: 60px;
            left: -18%;
            width: 130px;
            height: 130px;
            border-radius: 100px;
            transform: rotate(110deg);
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #142D6F 78.6%);
            @include max (1800px){
                display: none;
            }
        }
        &::after{
            position: absolute;
            left: 40px;
            content: "";
            top: 40%;
            width: 550px;
            height: 550px;
            border-radius: 100%;
            background: linear-gradient(47.56deg, #a3bdff -40.67%, #142d6f 78.6%);
            z-index: 0;
            transform: rotate(180deg);
            @include max (1800px){
                display: none;
            }
        }
        @include max (991px){
            padding-bottom: 40px;
        }
    }
}

