.lonyo-hero{
  &-section{
    padding: 230px 0 180px;
    position: relative;
    z-index: 0;
    @include max (991px){
      padding: 190px 0 150px;
    }
    @include max (767px){
      padding: 150px 0 100px;
    }
  }
  &-content{
    position: relative;
    max-width: 880px;
    @include max (1199px){
      max-width: 750px;
    }
    @include max (991px){
      max-width: 550px;
    }
    @include max (767px){
      max-width: 430px;
    }
    .hero-title{
      padding-bottom: 20px;
    }
    .text{
      max-width: 600px;
    }
  }
  &-thumb{
    text-align: center;
    position: relative;
    @include max (991px){
      padding-top: 40px;
    }
    &::before{
      content: "";
      position: absolute;
      top: 73px;
      right: -130px;
      width: 150px;
      height: 150px;
      border-radius: 100px;
      background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
      @include max (1560px){
          display: none;
      }
    }
    &::after{
      content: "";
      position: absolute;
      bottom: 73px;
      left: -100px;
      width: 90px;
      height: 90px;
      border-radius: 100px;
      background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
      @include max (1560px){
          display: none;
      }
    }
  }
  // end one
  &-section2{
    padding: 230px 0 400px;
    position: relative;
    z-index: 0;
    @include max (1399px){
      padding: 190px 0 330px;
    }
    @include max (991px){
      padding: 190px 0 300px;
    }
    @include max (767px){
        padding: 150px 0 100px;
    }
  }
  &-rating{
    padding-bottom: 15px;
    ul{
      li{
        color: var(--light-color);
        display: inline-block;
        margin-right: 5px;
        &:last-child{
          margin-right: 0;
        }
        img{
          margin-top: -6px;
        }
      }
    }
  }
  &-content2{
    .hero-title{
      color: var(--light-color);
    }
  }
  &-text2{
    padding-left: 60px;
    padding-top: 40px;
    @include max (1399px){
      padding-left: 30px;
    }
    @include max (991px){
      padding-left: 0;
    }
    @include max (767px){
      padding-top: 30px;
    }
    .text{
      color: var(--light-color);
      margin: 0;
    }
    ul{
      display: flex;
      gap: 20px;
      align-items: center;
      @include max (767px){
        flex-wrap: wrap;
      }
    }
  }
  &-dashbord{
    
  }
  // end two
  &-section3{
    padding: 230px 0 180px;
    position: relative;
    z-index: 0;
    background: linear-gradient(180deg, rgba(163, 189, 255, 0) 0%, #A3BDFF 100%);

    @include max (991px){
        padding: 190px 0 150px;
    }
    @include max (767px){
        padding: 150px 0 100px;
    }
  }
  &-content3{
    max-width: 1050px;
    text-align: center;
    margin: 0 auto;
    .hero-title{
      @include max (1199px){
        font-size: 70px;
      }
      @include max (767px){
        font-size: 48px;
      }
      @include max (575px){
        font-size: 35px;
      }
    }
    .text{
      max-width: 1000px;
      text-align: center;
      margin: 0 auto;
      padding-top: 20px;
      @include max (1399px){
        max-width: 800px;
      }
    }
  }
}
