.lonyo-countdown{
    &-wrap {
        display: flex;
        gap: 24px;
        justify-content: center;
        flex-wrap: wrap;
    }
    &-item{
        font-family: $heading-font;
        font-size: 110px;
        font-weight: 700;
        line-height: 110px;
        letter-spacing: -2px;
        color: var(--heading-color);
        position: relative;
        width: 250px;
        height: 250px;
        border-radius: 150px;
        border: 2px solid var(--heading-color);
        position: relative;
        padding: 55px 50px 73px 50px;
        text-align: center;
        h5{
            text-align: center;
        }
    }
  }