.lonyo-pricing{
    &-wrap{
        padding: 30px;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background-color: var(--white-bg);
        &.active{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-header{
        padding-bottom: 24px;
    }
    &-price{
        display: flex;
        align-items: flex-end;
        gap: 15px;
        span{
            font-size: 40px;
            font-weight: 600;
            line-height: 48px;
            letter-spacing: -1px;
            color: #001A3D33;
            text-decoration-line: line-through;
        }
    }
    &-text{
        padding: 30px 0;
        border-bottom: 1px solid #001A3D1A;
        margin-bottom: 30px;
    }
}