.lonyo-service{
    &-wrap{
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            box-shadow: -4px 10px 20px 0px #142D6F1A;
        }
    }
    &-title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 24px;
    }
    &-data{
        p{
            font-size: 18px;
            line-height: 1.39em;
        }
    }
}
.lonyo-content{
    &-thumb{
        text-align: center;
        position: relative;
        @include max (991px){
            padding-bottom: 40px;
        }
        &::before{
            position: absolute;
            content: "";
            top: 50%;
            left: 50%;
            z-index: -1;
            width: 550px;
            height: 550px;
            border-radius: 100%;
            transform: translate(-50%, -50%);
            background: linear-gradient(47.56deg, #A3BDFF -40.67%, #FFFFFF 78.6%);
        }
    }
    &-thumb2{
        text-align: center;
        @include max (991px){
            padding-bottom: 40px;
        }
    }
}

.lonyo-feature-shape{
    width: 130px;
    height: 130px;
    bottom: 45px;
    left: 6%;
    position: absolute;
    border-radius: 100px;
    background: linear-gradient(47.56deg, #A3BDFF -40.67%, #F6F4EF 78.6%);
    @include max (1800px){
        display: none;
    }

}

// add tabs

.tabs{
    display: flex;
    align-items: center;
    gap: 50px;
    transition: all 0.4s;
    flex-wrap: wrap;
    @include max (991px){
        gap: 30px;
    }
    .active-tab{
        &::before{
            content: "";
            bottom: 0;
            width: 100%;
            height: 3px;
            position: absolute;
            border-radius: 3px;
            transition: all 0.4s;
            margin-bottom: -10px;
            border-bottom: 3px solid var(--light-color2);
        }
    }
    li{
        display: inline-flex;
        gap: 10px;
        align-items: center;
        position: relative;
        &::before{
            content: "";
            bottom: 0;
            border-radius: 3px;
            width: 100%;
            height: 3px;
            position: absolute;
            margin-bottom: -10px;
            border-bottom: 3px solid var(--light-color);
        }
    }
 
}
.border-bottom{
    position: absolute;
    bottom: 224px;
    max-width: 1180px;
    left: 16%;
    border-bottom: 3px solid #F6F4EF1A!important;
    @include max (1800px){
        display: none;
    }
}

.tabs-content {
    margin-top: 30px;
    transition: all 0.4s;
	li {
		display: none;
	}
}
.lonyo-process{
    &-wrap{
        transition: all 0.4s;
        margin-top: 100px;
        padding-right: 43px;
        @include max (1199px){
            &:last-child{
                margin-top: 50px;
            }
        }
        @include max (991px){
            margin-top: 80px;
            &:last-child{
                margin-top: 30px;
            }
        }
        @include max (767px){
            margin-top: 60px;
        }
        &:hover{
            .lonyo-process-title{
                &::before{
                    display: block;
                }
            }
        }
    }
    &-number{
        padding-bottom: 24px;
    }
    &-title{
        padding-bottom: 30px;
        position: relative;
        &::before{
            content: "";
            width: 310px;
            height: 3px;
            transition: all 0.4s;
            bottom: 19px;
            left: 0;
            position: absolute;
            display: none;
            background-color: var(--light-color2);
        }
        h4{
            color: var(--light-color);
        }
    }
    &-data{
        p{
            margin: 0;
            font-size: 18px;
            font-weight: 400;
            line-height: 25px;
            color: var(--light-color);
        }
    }
}

