.lonyo-error{
    &-section{
        padding: 290px 0 120px;
        @include max (1199px){
            padding: 220px 0 120px;
        }
        @include max (991px){
            padding: 180px 0 80px;
        }
        @include max (767px){
            padding: 150px 0 60px;
        }
    }
    &-content{
        max-width: 800px;
        text-align: center;
        margin: 0 auto;
        .text{
            font-size: 21px;
            line-height: 30px;
            color: var(--heading-color);
            margin: 0 auto;
            max-width: 750px;
           

        }
    }
    &-thumb{
        padding-bottom: 45px;
        @include max (767px){
            padding-bottom: 30px;
        }
    }
}